
.description{
    font-size: 24px;
    font-weight: 600;
}

.currency{
    font-size: 18px;
    text-align: end;
}

.exchange_block{
    padding-top: 40px;
    display: flex;
    justify-content: space-around;
}

.exchange_form{
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 20px;
    padding-top: 20px;
    width: 200px;
    height: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border: 1px dashed var(--excretion-color);
    border-radius: 10px;
}

.exchange_select{
    font-weight: 500;
    width: 160px;
    height: 35px;
}
.exchange_input{
    font-weight: 500;
    width: 160px;
    height: 35px;
    padding-left: 10px;
    padding-right: 10px;
}